import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function date(_tableName: string) {
  const title = "Date"
  return genericColumn({
    endpoint: "companyIncomeStatement",
    columnKeys: ["date"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The day the data refers to.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyIncomeStatement?.data?.date}
          queryResult={info.row.original.financialData.companyIncomeStatement}
        >
          {info.row.original.financialData.companyIncomeStatement?.data?.date}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { date }
